import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Index',
        component: () => import('@/page/index/index')
    },
      {
          path: '/login',
          name: 'Login',
          component: () => import('@/page/login/index')
      },
      {
          path: '/register',
          name: 'Register',
          component: () => import('@/page/login/register')
      },
      {
          path: '/waterd',
          name: 'Waterd',
          component: () => import('@/page/waterd/index')
      },
      {
          path: '/mine',
          name: 'Mine',
          component: () => import('@/page/mine/index')
      },
      {
          path: '/wallet',
          name: 'Wallet',
          component: () => import('@/page/mine/wallet')
      },
      {
          path: '/transfer',
          name: 'Transfer',
          component: () => import('@/page/mine/transfer')
      },
      {
          path: '/withdraw',
          name: 'Withdraw',
          component: () => import('@/page/mine/withdraw')
      },
      {
          path: '/opinion',
          name: 'Opinion',
          component: () => import('@/page/mine/opinion')
      },
      {
          path: '/notice',
          name: 'Notice',
          component: () => import('@/page/mine/notice')
      },
      {
          path: '/noticedetail',
          name: 'Noticedetail',
          component: () => import('@/page/mine/noticedetail')
      },
      {
          path: '/invite',
          name: 'Invite',
          component: () => import('@/page/mine/invite')
      },
      {
          path: '/wechat',
          name: 'Wechat',
          component: () => import('@/page/mine/wechat')
      },
      {
          path: '/setting',
          name: 'Setting',
          component: () => import('@/page/mine/setting')
      },
      {
          path: '/rule',
          name: 'Rule',
          component: () => import('@/page/rule/index')
      },
      {
          path: '/backup',
          name: 'Backup',
          component: () => import('@/page/mine/backup')
      },
      {
          path: '/thirdRe',
          name: 'ThirdRe',
          component: () => import('@/page/mine/thirdRe')
      },
  ]
})



