
import Cryptojs from 'crypto-js'

// 创建
export function getSign(config) {

    let signStr = ''
    let params = []
    if (config.params) {

        // get
        params = config.params

    } else if (config.data) {
        // post
        params = config.data
    }

    // url参数签名
    let pArray = []
    for (let p in params) {
        pArray.push(p)
    }
    let sArray = pArray.sort()
    for (let item of sArray) {
        signStr += item +'=' + params[item] + '&'
    }
    signStr =  signStr.slice(0, -1)
    let key = 'wtreeappkey'
    let hash = Cryptojs.HmacSHA256(signStr, key).toString()
    return hash
}



