import { login, logout, getInfo, uploadWechat } from '../../api/user'
import { getToken, setToken, removeToken } from '@/utils/auth'

const state = {
  token: getToken(),
  name: '',
  avatar: '',
    wechat: '',
}

const mutations = {
  SET_TOKEN: (state, token) => {
    state.token = token
  },
  SET_NAME: (state, name) => {
    state.name = name
  },
  SET_AVATAR: (state, avatar) => {
    state.avatar = avatar
  },
  SET_WECHAT: (state, wechat) => {
    state.wechat = wechat
  },
}

const actions = {
  // user login
  login({ commit }, userInfo) {
    const { username, password } = userInfo
    return new Promise((resolve, reject) => {
      login({ username: username.trim(), password: password }).then(response => {
        const data = response
        commit('SET_TOKEN', data.token)
        setToken(data.token)
        resolve(data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  // get user info
  getInfo({ commit, state }) {
    return new Promise((resolve, reject) => {
      getInfo(state.token).then(response => {
        const data  = response
        if (!data) {
          reject('Verification failed, please Login again.')
        }

        const { nickname, avatar, wechat_code } = data

        commit('SET_NAME', nickname)
        commit('SET_AVATAR', avatar)
        commit('SET_WECHAT', wechat_code)
        resolve(data)
      }).catch(error => {
        reject(error)
      })
    })
  },

  // user logout
  logout({ commit, state }) {
    return new Promise((resolve, reject) => {
      logout(state.token).then(() => {
          commit('SET_TOKEN', '')
          commit('SET_NAME', '')
          commit('SET_AVATAR', '')
          commit('SET_WECHAT', '')
        removeToken()
        resolve()
      }).catch(error => {
        reject(error)
      })
    })
  },

  // remove token
  resetToken({ commit }) {
    return new Promise(resolve => {
      commit('SET_TOKEN', '')
      removeToken()
      resolve()
    })
  },

    setWechat({ commit }, img) {
        return new Promise(resolve => {
            commit('SET_WECHAT', img)
            resolve()
        })
    },
    setAvatar({ commit}, img) {
        return new Promise(resolve => {
            commit('SET_AVATAR', img)
            resolve()
        })
    },
    setName({ commit}, name) {
        return new Promise(resolve => {
            commit('SET_NAME', name)
            resolve()
        })
    },
}

export default {
  namespaced: true,
  state,
  mutations,
  actions
}




